@charset "UTF-8";
/* reset */
/* main variables */
body { margin: 0px; padding: 0px; font-family: Arial, sans-serif; background-color: #fafafa; height: 100%; line-height: 1; position: relative; font-size: 13px !important; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

/*MAIN*/
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; font-size: 100%; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main { display: block; }

blockquote, q { quotes: none; }

blockquote:before, blockquote:after, q:before, q:after { content: ''; content: none; }

table { border-collapse: collapse; border-width: 0px; padding: 0px; margin: 0px; }

html { height: 100%; -webkit-text-size-adjust: none; -ms-text-size-adjust: none; }

input, textarea { color: #333; font-family: Arial, sans-serif; outline: none; border-radius: 0; -moz-border-radius: 0; -webkit-border-radius: 0; -webkit-appearance: none; }

input[type="button"], input[type="submit"], button { cursor: pointer; }

td { margin: 0px; padding: 0px; }

form { padding: 0px; margin: 0px; }

a { color: #100da9; -webkit-transition: all 0.2s linear; -moz-transition: all 0.2s linear; -ms-transition: all 0.2s linear; -o-transition: all 0.2s linear; transition: all 0.2s linear; text-decoration: none; outline: none; -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

a, span, div, button { outline: none !important; }

input[type=submit], input[type=button], button { -webkit-appearance: none; outline: none; }

* { -webkit-box-sizing: border-box; -moz-box-sizing: border-box; box-sizing: border-box; }

*:before, *:after { -webkit-box-sizing: border-box; -moz-box-sizing: border-box; box-sizing: border-box; }

.clearfix:after, .wrapper:after, .row:after { content: "."; display: block; height: 0; clear: both; visibility: hidden; }

@font-face { font-family: 'Comfortaa'; src: url("../fonts/Comfortaa-Bold.eot"); src: url("../fonts/Comfortaa-Bold.woff") format("woff"), url("../fonts/Comfortaa-Bold.ttf") format("truetype"), url("../fonts/Comfortaa-Bold.svg") format("svg"); font-weight: 700; font-style: normal; unicode-range: U+0020-FFFD; }

@font-face { font-family: 'Open Sans'; src: url("../fonts/OpenSans-Regular.ttf"); src: url("../fonts/OpenSans-Regular.ttf?#iefix") format("embedded-opentype"); font-weight: 400; font-style: normal; font-stretch: normal; unicode-range: U+0020-FFFD; }

@font-face { font-family: 'Open Sans'; src: url("../fonts/OpenSans-Bold.ttf"); src: url("../fonts/OpenSans-Bold.ttf?#iefix") format("embedded-opentype"); font-weight: 700; font-style: normal; font-stretch: normal; unicode-range: U+0020-FFFD; }

@font-face { font-family: 'Open Sans'; src: url("../fonts/OpenSans-SemiBold.ttf"); src: url("../fonts/OpenSans-SemiBold.ttf?#iefix") format("embedded-opentype"); font-weight: 600; font-style: normal; font-stretch: normal; unicode-range: U+0020-FFFD; }

/* Slider */
.slick-slider { position: relative; display: block; -moz-box-sizing: border-box; box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; margin-right: 25px; display: none; }

.slick-slide:last-child { margin-right: 0; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-arrow.slick-hidden { display: none; }

/* Slider */
/* Icons */
@font-face { font-family: "slick"; src: url("../fonts/slick.eot"); src: url("../fonts/slick.eot?#iefix") format("embedded-opentype"), url("../fonts/slick.woff") format("woff"), url("../fonts/slick.ttf") format("truetype"), url("../fonts/slick.svg#slick") format("svg"); font-weight: normal; font-style: normal; }

/* Arrows */
.slick-prev, .slick-next { position: absolute; display: block; height: 20px; width: 20px; line-height: 0px; font-size: 0px; cursor: pointer; background: transparent; color: transparent; top: 50%; -webkit-transform: translate(0, -50%); -ms-transform: translate(0, -50%); transform: translate(0, -50%); padding: 0; border: none; outline: none; }

.slick-prev:hover, .slick-prev:focus, .slick-next:hover, .slick-next:focus { outline: none; background: transparent; color: transparent; }

.slick-prev:hover:before, .slick-prev:focus:before, .slick-next:hover:before, .slick-next:focus:before { opacity: 1; }

.slick-prev.slick-disabled:before, .slick-next.slick-disabled:before { opacity: 0.25; }

.slick-prev:before, .slick-next:before { font-family: "slick"; font-size: 20px; line-height: 1; color: white; opacity: 0.75; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.slick-prev { left: -25px; }

[dir="rtl"] .slick-prev { left: auto; right: -25px; }

.slick-prev:before { content: "←"; }

[dir="rtl"] .slick-prev:before { content: "→"; }

.slick-next { right: -25px; }

[dir="rtl"] .slick-next { left: -25px; right: auto; }

.slick-next:before { content: "→"; }

[dir="rtl"] .slick-next:before { content: "←"; }

/* Dots */
.slick-dotted.slick-slider { margin-bottom: 30px; }

.slick-dots { position: absolute; bottom: -25px; list-style: none; display: block; text-align: center; padding: 0; margin: 0; width: 100%; }

.slick-dots li { position: relative; display: inline-block; height: 20px; width: 20px; margin: 0 5px; padding: 0; cursor: pointer; }

.slick-dots li button { border: 0; background: transparent; display: block; height: 20px; width: 20px; outline: none; line-height: 0px; font-size: 0px; color: transparent; padding: 5px; cursor: pointer; }

.slick-dots li button:hover, .slick-dots li button:focus { outline: none; }

.slick-dots li button:hover:before, .slick-dots li button:focus:before { opacity: 1; }

.slick-dots li button:before { position: absolute; top: 0; left: 0; content: "•"; width: 20px; height: 20px; font-family: "slick"; font-size: 6px; line-height: 20px; text-align: center; color: black; opacity: 0.25; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.slick-dots li.slick-active button:before { color: black; opacity: 0.75; }

/*
================================================================================
|                                     HEADER                                   |
================================================================================
*/
header { background: #f0f0f0; width: 100%; height: 80px; min-width: 320px; text-align: left; order: 1; -webkit-order: 1; }

/*
================================================================================
|                                     FOOTER                                   |
================================================================================
*/
footer { background: #000; height: 80px; text-align: left; width: 100%; min-width: 320px; order: 3; -webkit-order: 3; }

.main-wrapper { padding: 0 0 0 0; min-width: 320px; width: 100%; position: relative; overflow: hidden; min-height: 100%; display: -webkit-box; display: -moz-box; display: -ms-flexbox; display: -webkit-flex; display: flex; flex-direction: column; -webkit-flex-direction: column; -ms-flex-direction: column; justify-content: flex-start; -webkit-justify-content: flex-start; -ms-flex-pack: start; }

.wrapper { min-width: 320px; max-width: 1200px; padding: 0 0 0 15px; margin: 0 auto; position: relative; }

/* titles */
p { font-size: 13px; line-height: 18px; color: #444; padding: 15px 0 15px 0; font-weight: 400; }

h1 { font-size: 30px; line-height: 38px; color: #2b2b2b; font-family: "Open Sans", sans-serif; padding: 15px 0 15px 0; font-weight: 400; }

h2 { font-size: 16px; line-height: 22px; color: #2b2b2b; font-family: "Open Sans", sans-serif; padding: 18px 0 15px 0; font-weight: 400; }

h3 { font-size: 13px; line-height: 18px; color: #2b2b2b; font-family: "Open Sans", sans-serif; padding: 17px 0 18px 0; font-weight: 700; }

/* text position */
.text-left { text-align: left !important; }

.text-center { text-align: center !important; }

.text-right { text-align: right !important; }

.nowrap { white-space: nowrap !important; }

/* loader */
.loaded .main-wrapper { visibility: hidden; }

.icon-load { background: url(../img/loader.gif) no-repeat left top; width: 40px; height: 40px; position: fixed; left: 50%; top: 50%; margin-left: -20px; margin-left: -20px; display: none; }

.loaded .icon-load { display: block; }

.btn { background-color: #FFF; padding: 10px 15px; border-width: 1px; border-style: solid; border-color: rgba(72, 179, 203, 0.5); min-height: 30px; width: 100%; height: 100%; text-transform: uppercase; }

/*
================================================================================
|                                     CONTENT                                 |
================================================================================
*/
.content { min-width: 320px; text-align: left; width: 100%; order: 2; -webkit-order: 2; flex-grow: 1; -webkit-flex-grow: 1; background: url("../img/bg-pattern.jpg") repeat, #f9f9f9; }

.content__title { margin: 25px 0 27px 25px; letter-spacing: -3px; }

.products { display: flex; }

.products__filters { min-width: 270px; margin-right: 27px; }

.products__item { overflow: hidden; }

.products__recommendations { overflow: hidden; margin-bottom: 20px; position: relative; }

.cards { display: flex; flex-flow: row wrap; flex: auto; padding: 2px; transform: translateY(-2px); }

.cards__item { margin-right: 27px; margin-bottom: 18px; width: 30.5%; position: relative; }

.cards__item:nth-child(3n) { margin-right: 0; }

.card { box-sizing: border-box; display: flex; flex-direction: column; align-items: center; background-color: #FFF; margin-top: 3px; -webkit-transition: all 0.2s linear; -moz-transition: all 0.2s linear; -ms-transition: all 0.2s linear; -o-transition: all 0.2s linear; transition: all 0.2s linear; border-width: 1px; border-style: solid; border-color: #fafafa; -webkit-box-shadow: 0px 0px 4px 0px rgba(68, 68, 68, 0.2); -moz-box-shadow: 0px 0px 4px 0px rgba(68, 68, 68, 0.2); -ms-box-shadow: 0px 0px 4px 0px rgba(68, 68, 68, 0.2); -o-box-shadow: 0px 0px 4px 0px rgba(68, 68, 68, 0.2); box-shadow: 0px 0px 4px 0px rgba(68, 68, 68, 0.2); -webkit-border-radius: 5px; -moz-border-radius: 5px; border-radius: 5px; }

.card__img { width: 100%; }

.card__img img { display: block; width: 100%; object-fit: cover; }

.card__rating { margin-top: 20px; }

.card__divider { margin-bottom: 17px; }

.card__price { font-size: 16px; line-height: 22px; color: #2b2b2b; font-weight: 700; margin-bottom: 16px; }

.card__title { -webkit-transition: all 0.2s linear; -moz-transition: all 0.2s linear; -ms-transition: all 0.2s linear; -o-transition: all 0.2s linear; transition: all 0.2s linear; margin: 0 5px; text-align: center; }

.divider { width: 80px; height: 1px; background-color: #e1e1e1; }

.sign { display: inline-block; height: 28px; font-size: 11px; line-height: 30px; color: #fff; -webkit-border-radius: 3px; -moz-border-radius: 3px; border-radius: 3px; font-weight: 600; vertical-align: middle; text-transform: uppercase; padding: 0 8px; position: absolute; top: 10px; left: 10px; }

.sign_info { background-color: #48b3cb; }

.sign_attention { background-color: #ffae2e; }

.recommendations__title { text-transform: uppercase; }

.filters { position: relative; box-sizing: border-box; background-color: #FFF; border-width: 1px; border-style: solid; border-color: #e1e1e1; }

.filters__accordion.active { display: block; }

.filters__btn { display: none; justify-content: center; align-items: center; position: absolute; top: 0; left: 0; width: 100%; height: 100%; }

.rating { position: relative; height: 15px; }

.stars { width: 92px; }

.stars_empty { position: absolute; top: 0; left: calc(50% - 46px); overflow: hidden; width: 90px; }

.stars_full { left: calc(50% - 46px); overflow: hidden; position: absolute; top: 0; width: 100%; }

/*Slider*/
.multiple-items .card { text-align: center; margin: 3px auto; }

.multiple-items .divider { margin-left: auto; margin-right: auto; }

.slick-track { left: 3px; }

.slick-dots { bottom: -68px; transform: translateX(-10px); }

.slick-dots li, .slick-dots li button, .slick-dots li button:before { width: 12px; }

.slick-dots li { margin: 0; }

.slick-dots li.slick-active button:before { color: #e44256; }

.slick-slide { margin-right: 27px; }

.slick-dotted.slick-slider { margin-bottom: 120px; }

.arrows { position: absolute; bottom: 38px; left: 0; z-index: 10; width: 100%; height: 40px; }

.arrows__symbol { line-height: 38px; transform: translate(-1px, -2px); font-size: 24px; line-height: 24px; color: #48b3cb; font-weight: 700; }

.arrows__prev, .arrows__next { box-sizing: border-box; display: flex; justify-content: center; align-items: center; width: 38px; height: 38px; background-color: #fafafa; border-width: 1px; border-style: solid; border-color: #e1e1e1; -webkit-border-radius: 50%; -moz-border-radius: 50%; border-radius: 50%; position: absolute; top: 0; }

.arrows__prev { left: calc(50% - 80px); }

.arrows__next { left: calc(50% + 25px); }

/*atomic*/
.w-80 { width: 80%; }

.red { background-color: red; }

@media not all and (hover: none) { .card:hover { border-width: 1px; border-style: solid; border-color: rgba(72, 179, 203, 0.3); -webkit-box-shadow: 0px 4px 9px 0px rgba(72, 179, 203, 0.4); -moz-box-shadow: 0px 4px 9px 0px rgba(72, 179, 203, 0.4); -ms-box-shadow: 0px 4px 9px 0px rgba(72, 179, 203, 0.4); -o-box-shadow: 0px 4px 9px 0px rgba(72, 179, 203, 0.4); box-shadow: 0px 4px 9px 0px rgba(72, 179, 203, 0.4); -webkit-transition: all 0.2s linear; -moz-transition: all 0.2s linear; -ms-transition: all 0.2s linear; -o-transition: all 0.2s linear; transition: all 0.2s linear; }
  .card:hover .card__title { -webkit-transition: all 0.2s linear; -moz-transition: all 0.2s linear; -ms-transition: all 0.2s linear; -o-transition: all 0.2s linear; transition: all 0.2s linear; color: #48b3cb; letter-spacing: 1px; } }

@media screen and (max-width: 991px) { .wrapper { padding: 0 15px 0 30px; }
  .cards__item { width: 47%; }
  .cards__item:nth-child(3n) { margin-right: 3%; }
  .cards__item:nth-child(2n) { margin-right: 0; } }

@media screen and (max-width: 767px) { .wrapper { padding: 0 50px; }
  .content__title { text-align: center; margin: 20px auto; }
  .filters { width: 100%; border-width: 1px; border-style: solid; border-color: #fafafa; }
  .filters__accordion { display: none; }
  .filters__btn { display: flex; }
  .products { flex-direction: column; justify-content: center; }
  .products__filters { height: 50px; margin: 0 auto 30px; }
  .cards__item { width: 100%; margin-right: 0; margin-bottom: 30px; }
  .cards__item:nth-child(3n), .cards__item:nth-child(2n) { margin-right: 0; } }
